<template lang="pug">
  .hello
    .ui.container
      sui-segment(raised='').ranged
        sui-header(size="large") {{ msg }}
        router-link(to="/payment")
          img.logo(src="../assets/logo.png")

      sui-grid(:columns="3", divided)
        sui-grid-row
        sui-grid-row
          sui-grid-column
            sui-header 
            sui-list(bulleted='' align="left")
              sui-list-item 
              sui-list-item 
              sui-list-item 
              sui-list-item 
          sui-grid-column
          sui-grid-column
            sui-header Haskell
            sui-list(bulleted='' align="left")
              sui-list-item 
              sui-list-item 
              sui-list-item 
              sui-list-item 
        sui-grid-row
          sui-grid-column
          sui-grid-column
            a(href="https://docs.google.com/forms/d/e/1FAIpQLSduucHqWhiSAP7_XZbKjvLGxaQrLP79H5B1a6GwFQRL-MNSLA/viewform", target="_blank") 
          sui-grid-column
        sui-grid-row
</template>

<script>
export default {
  name: 'Flow',
  data () {
    return {
      msg: ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



