<template lang="pug">
  .hello
    .ui.container
      sui-segment(raised='').heart
        sui-header(size="large") {{ msg }}
        router-link(to="/faq")
          img.logo(src="../assets/logo.png")
        sui-header(size="large") ?
          .ui.sub.header ~
        br
        br
        sui-grid(:columns="2" divided)
          sui-grid-row
            sui-grid-column
              sui-header(size="medium") 
                .ui.sub.header
                  | 
            sui-grid-column
              sui-header(size="medium") Haskell
                .ui.sub.header
                  | $NT1000 / 
</template>
<script>
export default {
  name: 'Payment',
  data () {
    return {
      msg: ''
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.tada {
  position: relative;
  animation: tada 4s infinite;
}

</style>



